}

.flash__error {
  background: #ef9a9a;
  border: 1px solid #E57373;
  color: #b71c1c;
}

.recipe__header {
  box-shadow: rgba(0, 0, 0, 0.09) 0 1px 0;
  background: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0px 15px;
  margin-top: 10px;
}

.recipe__list {
  width: 960px;
  margin: 0 auto;
  padding-top: 25px;
  display: flex;
  flex-wrap: wrap;
}

.recipe__item {
  width: 33.333%;
}

.recipe__inner {
  display: block;
  box-shadow: rgba(0, 0, 0, 0.09) 0 1px 0;
  background: white;
  margin: 10px;
}

.recipe__name {
  padding: 10px;
  margin: 0px;
}

.recipe__show {
  width: 960px;
  margin: 0 auto;
  padding: 15px;
}

.recipe__row {
  display: flex;
  flex-wrap: wrap;
  margin-top: 25px;
}

.recipe__box {
  box-shadow: rgba(0, 0, 0, 0.09) 0 1px 0;
  background: white;
  padding: 15px;
}

.recipe__title {
  color: #E91E63;
}

.recipe__image {
  width: 40%;
}

.recipe__details {
  width: 60%;
}

.recipe__details_inner {
  margin-left: 25px;
  box-shadow: rgba(0, 0, 0, 0.09) 0 1px 0;
  background: white;
  padding: 15px;
}

.recipe__pantry {
  width: 60%;
}

.recipe__pantry_inner {
  margin-left: 25px;
  box-shadow: rgba(0, 0, 0, 0.09) 0 1px 0;
  background: white;
  padding: 15px;
}

.recipe__ingredients {
  width: 40%;
}

.recipe__ingredients ul {
  margin: 0;
  padding: 0;
}

.recipe__ingredients li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 0px;
  border-top: 1px dashed #e9e9e9;
}

.recipe__directions {
  width: 60%;
}

.recipe__directions ul {
  padding: 0;
}

.recipe__directions li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-top: 1px dashed #e9e9e9;
}

.recipe__directions_inner {
  margin-left: 25px;
  box-shadow: rgba(0, 0, 0, 0.09) 0 1px 0;
  background: white;
  padding: 15px;
}

.recipe__ingredient {
  display: flex;
  justify-content: space-around;
  margin-bottom: 10px;
}

.recipe__pantry__ingredient {
  display: inline-block;
  padding: 0.4rem;
  background-color: #002b36;
  color: white;
  border-radius: 0.2rem;
  margin: 0.3rem 0.3rem 0 0;
  cursor: -webkit-grab;
}

.recipe__rows {
  min-height: 10rem;
  position: relative;
}

.recipe__rows--empty::after {
  content: 'Drag some ingredients here to start your recipe.';
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  height: 4rem;
  text-align: center;
}

.recipe__nutrition {
  width: 40%;
}

.grabber::before {
  content: "\f142 \f142";
  letter-spacing: 0.1em;
  font-family: FontAwesome;
  margin-right: 0.4rem;
  font-size: 0.9rem;
  position: relative;
  top: 0rem;
  margin-left: 0.1rem;
  color: #0091b6;
}

.nutrition-row {
  border-bottom: 2px solid black;
  padding-bottom: 0.4rem;
  display: Flex;
  line-height: 2.2;
}

.nutrition-row select {
  max-width: 10rem;
  display: inline-block;
}

.nutrition-row__unit {
  white-space: nowrap;
  flex-grow: 0;
  flex-shrink: 0;
  text-transform: capitalize;
}

.nutrition-row__value {
  text-align: right;
  width: 100%;
}

.drop-zone {
  border: 0.3rem dashed #002b36;
  animation-name: pulse;
  animation-duration: 2s;
  animation-iteration-count: infinite;
}

.sortable-ghost {
  margin: 1rem;
}

@keyframes pulse {
  0% {
    border-color: #002b36;
  }
  25% {
    border-color: #006883;
  }
  50% {
    border-color: #002b36;
  }
  75% {
    border-color: #006883;
  }
  100% {
    border-color: #002b36;
  }
}

.empty-message {
  text-align: center;
  width: 100%;
  margin-top: 2rem;
}

.row {
  background-color: #FAFAFA;
  padding: 0.4rem;
  border: 1px solid #EEEEEE;
  border-radius: 0.2rem;
}

.row__segment {
  width: 33.33333%;
  line-height: 2.4;
}

.row__handle {
  padding: 0.6rem 0.2rem 0rem 0.1rem;
}

.image__upload {
  height: 257px;
  background: #FAFAFA;
  border: 1px dashed rgba(0, 0, 0, 0.1);
  display: flex;
  justify-content: center;
  align-items: center;
}

.image__preview {
  position: relative;
}

.image__close {
  position: absolute;
  right: 0;
  top: 0;
}

.not__found {
  box-shadow: rgba(0, 0, 0, 0.09) 0 1px 0;
  background: white;
  padding: 15px;
  width: 960px;
  margin: 30px auto;
}

.edit-ingredient-bar {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  width: 12rem;
  background-color: rgba(0, 0, 0, 0.4);
  opacity: 0;
  transition: opacity 400ms;
  text-align: center;
}

.edit-ingredient-bar.active {
  opacity: 1;
}

.edit-ingredient-bar .inner {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.edit-ingredient-bar .message {
  top: 50%;
  position: absolute;
  font-weight: bold;
  color: #002b36;
  width: 100%;
  font-size: 1rem;
}

.ingredient-form {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.ingredient-form .form__container {
  width: 100%;
  min-width: 17rem;
  background-color: #EEE;
}

@media (min-width: 40em) {
  .ingredient-form .form__container {
    width: 49%;
  }
}

@media (min-width: 60em) {
  .ingredient-form .form__container {
    width: 30%;
  }
}

.ingredient-list {
  margin: 0;
  list-style-type: none;
  padding: 0;
}

@media (min-width: 60em) {
  .ingredient-list {
    column-count: 2;
  }
}

.ingredient-list__heading {
  background-color: #007c9c;
  padding: 0.4rem;
  display: block;
  color: white;
  font-weight: bold;
}

.ingredient-list__row a {
  padding: 0.4rem;
  cursor: pointer;
  display: block;
}

.ingredient-list__row a:hover {
  background-color: #EEEEEE;
}

.ingredient-list__child-list {
  list-style-type: none;
  margin: 0.5rem 0;
  padding: 0rem 0 0 1rem;
}
